import React, { Component , Fragment} from 'react';
import ReactDOM from 'react-dom'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  Link
} from 'react-router-dom';
import './App.scss';

import createClass from 'create-react-class';

import Main from './Components/Main';
import Test from './Components/Test';
import Csv from './Components/Csv';

var App = createClass({
    render: function () {
    return (
      <Router>
        <div id="app" className="App" style={{margin:"0px auto"}}>
          <Fragment>
              <Switch>
              <Route exact path="/main" exact render={() => (

                  <Main></Main>
                )} />
              <Route exact path="/test" exact render={() => (

                  <Test></Test>
                )} />
              <Route exact path="/" exact render={() => (

                  <Csv></Csv>
                )} />

                </Switch>
              </Fragment>
              
            </div>
          </Router>

        )}
})

export default App;